  justify-content: flex-end;
  align-items: center;
  align-content: center;
  display: flex;
  margin-bottom: 28px;
  padding-right: 11px;
}

*{
    box-sizing: border-box;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #2b323c;
    color: #fff;
    -webkit-animation: bgi 2s ease;
            animation: bgi 2s ease;
    font-weight: 200;
    
}




.container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid transparent;
    border-radius: 11px;
    background-color: rgb(14, 76, 105);
    margin: 19px 9px;
    color: rgb(4, 67, 46);
}
@-webkit-keyframes bg{
    0%{
        width: 10%;
    }
    50%{
        width: 10%;
    }
    100%{
        width: 100%
    }
}
@keyframes bg{
    0%{
        width: 10%;
    }
    50%{
        width: 10%;
    }
    100%{
        width: 100%
    }
}


