}
form {
  justify-content: center;
  align-items: center;
  align-content: center;
}
input[type="submit"] {
  background-color: rgb(3, 34, 79);
  color: #fff;
  padding: 4px 20px;
  cursor: pointer;
}
input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button{
    -webkit-appearance: none;
    
}

input[type=number]{
    list-style: none;
    
}

